.navigation {
  background: linear-gradient(to right, rgb(247, 237, 96),rgb(238, 91, 6)); /* Ajustez les couleurs et la direction selon votre besoin */
  margin-top: 0px;  
  
    .video-banner {
      display: flex;           // Active Flexbox
      justify-content: center; // Centrage horizontal
      align-items: center;     // Centrage vertical
      height: 100%;            // Prend toute la hauteur disponible
  
      video {
        max-width: 50%;
        height: 50%; 
        object-fit: cover; // maintient le rapport d'aspect de la vidéo
        margin-bottom: 0px;
       // border-radius: 8px;
       // box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
        
      }
    }
  ul {
    display: flex;
    margin: 10px;
    font-size: 25px;

    a {
      text-decoration: none;
      padding: 10px;

      li {
        color: #2196F3;
        transition: 0.2s;
        cursor: pointer;

        &:hover {
          color:#0067bb;
        }
      }
    }

    .nav-active {
      position: relative;

      &::after {
        content: "";
        height: 4px;
        width: 0;
        background: #2196F3;
        position: absolute;
        border-radius: 10px;
        bottom: 0px;
        left: 50%;
        transform: translateX(-50%);
        -webkit-animation: anim 0.3s ease forwards;
        animation: anim 0.6s ease forwards;

        @-webkit-keyframes anim {
          to {
            width: 100%;
          }
        }
        @keyframes anim {
          to {
            width: 100%;
          }
        }
      }
    }
  }
}
