/* Style général de la page */
body {
    font-family: 'Arial', sans-serif;
    line-height: 1.6;
    
}
.app-description{
    margin: 80px;
}


/* Style pour la section de description de l'application */
.app-description h1 {
    text-align: center;
    color: #333;
    margin-top: 0px;
    margin-bottom: 80px;

}

/* Style pour les bulles de contenu */
.bubble {
    padding: 20px;
    margin-bottom: 20px;
    border-radius: 20px;
    background-color: #efebe2;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    max-width: 600px; /* Ensures the bubbles don't get too wide */
    opacity: 1;
  
  transition: opacity 0.6s ease-out, transform 0.6s ease-out;
}

/* Style pour les titres des bulles */
.bubble h3 {
    color: #2c3e50;
    margin-bottom: 10px;
}

/* Style pour les paragraphes à l'intérieur des bulles */
.bubble p {
    font-size: 16px;
    color: #555;
   
    
}

// La classe qui sera ajoutée par l'IntersectionObserver
.slide-in {
    opacity: 1;
    transform: translate(0);
  }
  


/* Alternance des bulles à gauche et à droite */
.left {
   // background-color: #eaf0f6;
    float: left; /* Align to the left */
    clear: both; /* Ensures the bubbles don't overlap */
    margin-right: 10%; /* Provides space on the right */
    transform: translateX(-100%);
}

.right {
   // background-color: #f4f4f4;
    float: right; /* Align to the right */
    clear: both; /* Ensures the bubbles don't overlap */
    margin-left: 10%; /* Provides space on the left */
    transform: translateX(100%);
}

/* Adaptation pour les petits écrans */
@media (max-width: 768px) {
    .left, .right {
        float: none; /* Stack bubbles vertically on small screens */
        margin-left: auto;
        margin-right: auto;
        max-width: 90%;
    }
}
